.cinema{
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(16,40,61,1) 15%);
    padding: 30px;
    position: relative;
    margin: 0;
}
.cinemaController{
    display: inline-block;
    position: relative;
    align-items: center;
}
.title{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: block;
    pointer-events: none;
}
.img{
    width: 300px;
    height: 400px;
    padding: 10px;
    position: relative;
    display: flex;
    overflow: hidden;
    text-decoration: none;
}
.cinemaController{
    padding: 35px;
    margin:20px 20px 0 0;
    display: flex;
    align-items: end;
   position: relative;
}
.rating:hover{
    color:palegreen
}
.rating:hover{
    filter: brightness(60%);
}

.rating{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-style: normal;
    color: rgb(0, 255, 0);
    position: absolute;
    z-index: 1;
    margin: 40px 0 0 0 ;
    font-size: 30px;
    padding: 10px 10px 10px 0px;
}
.text{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-style: normal;
    color: white;
    z-index: 1;
    font-size: 30px;
    padding: 5px 15px 0 0;
    margin: 10px;
}
span{
    text-decoration: none;
}
li{
    text-decoration: none;
}